<template>
  <div>
    <div class="container">
      <div class="row marketing">
        <div class="col-xs-8">
          <div class="panel-key">
            <div class="panel-wrap">
              <dl class="clearfix">
                <dt class="metatit">文件</dt>
                <dd>
                  <file-upload
                    title="文件选择"
                    class="btn btn-secondary file-upload"
                    :events="events"
                    name="xls"
                    post-action="/upload/xls"
                    extensions="xls,xlsx"
                    :files="files"
                    ref="upload">
                  </file-upload>
                </dd>
              </dl>
              <dl class="clearfix">
                <dt class="metatit">算法</dt>
                <dd>
                  <ul class="algorithm-select-list clearfix">
                    <li v-for="item in algorithmList">
                      <a class="algorithm-select-item" v-on:click="select(item)"
                         v-bind:class="{ 'algorithm-selected': item.isSelected }" >
                        {{item.name}}
                      </a>
                    </li>
                  </ul>
                </dd>
              </dl>
              <dl class="clearfix">
                <dt class="metatit">备注</dt>
                <dd>
                  <div class="input-remarks">
                    <input type="text" v-model="remark" class="form-control" placeholder="">
                  </div>
                </dd>
              </dl>
              <div class="panel-addTask">
                <button type="button" v-on:click="addTask" class="btn btn-danger addTaskbtn">添加到任务列表</button>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>
<script>
    import FileUpload from 'vue-upload-component';
    /* eslint no-param-reassign: ["error", { "props": false }] */
    /* eslint no-console: ["error", { allow: ["warn", "error"] }] */
    export default{
      components: {
        FileUpload,
      },
      data() {
        return {
          message: {
            isError: false,
            msg: '',
            msgTemplate: {
            },
          },
          /* 提交任务表单 */
          algorithm: '',
          remark: '',
          /* 算法参数 */
          algorithmList: [
            {
              name: 'IRE',
              args: '',
              isSelected: false,
            },
            {
              name: 'CRE',
              args: '',
              isSelected: false,
            },
            {
              name: 'ICRE',
              args: '',
              isSelected: false,
            },
            {
              name: 'ILRE',
              args: '',
              isSelected: false,
            },
          ],
          select(item) {
            this.algorithm = item.name;
            this.algorithmList.forEach((ele) => {
              ele.isSelected = false;
            });
            item.isSelected = true;
          },
          /*
            上传插件配置
           */
          files: [],
          upload: [],
          events: {
            add(file, component) {
              component.active = true;
              file.headers['X-Filename'] = encodeURIComponent(file.name);
              file.data.finename = file.name;
            },
          },
        };
      },
      computed: {
        fileName() {
          console.warn(this.files);
          return this.files[0].response.data.fileId;
        },
        uploadSuccess() {
          if (this.files[0].success) {
            return false;
          }
          return true;
        },
      },
      methods: {
        addTask() {
          this.$http.post('/calculation/cal',
            {
              fileName: this.fileName,
              algoName: this.algorithm,
              remark: this.remark,
            })
            .then((res) => {
              console.warn(res);
            }, (err) => {
              console.error(err);
            });
        },
      },
      mounted() {
        this.upload = this.$refs.upload.$data;
      },
    };
</script>
<style>
  label {
    display: inline-block;
    margin-bottom: 0;
  }
  .panel-key {
    margin: 0 20px 24px 10px;
  }
  .panel-wrap {
    color: #666;
    padding: 0 0 10px;
  }
  .panel-wrap dl {
    padding-bottom: 6px;
  }
  .panel-wrap dl .metatit{
    color: #838383;
    text-align: left;
    float: left;
    width: 54px;
  }
  .panel-wrap dl dd{
    width: 350px;
    float: left;
  }
  .panel-wrap .algorithm-select-list{
    margin: 0px;
    padding: 0px;
    list-style: none;
  }
  .panel-wrap .algorithm-select-list li{
    float: left;
    position: relative;
    margin: 0 4px 4px 0;
    line-height: 28px;
    vertical-align: middle;
    padding: 2px;
    cursor: pointer;
  }
  .panel-wrap .algorithm-select-list li a:hover{
    border: 1px solid #bd2013;
  }
  .panel-wrap .algorithm-select-list li a {
    float: left;
    background-color: #fff;
    white-space: nowrap;
    width: auto!important;
    min-width: 10px;
    padding: 0 9px;
    text-align: center;
    border: 1px solid #b8b7bd;
    text-decoration: none;
  }
  .panel-wrap .algorithm-select-list li .algorithm-selected{
    border: 1px solid #bd2013;
  }
  .panel-addTask{
    padding: 10px 0 0 66px;
    margin: 10px 0 0;
  }
  .panel-addTask .addTaskbtn{
    margin-right: 10px;
    float: left;
  }
  .panel-remarks .input-remarks{
    position: relative;
    display: table;
    border-collapse: separate;
  }
</style>



